import CardLayout from '~/components/CardLayout.vue';

export default {
	async asyncData (context) {
		const query = `
query{
	user(id:"${context.params.id}"){
              username
              confirmed
              hideouts(where:{
                isDeleted_ne:true
              }){
                hideoutId
                nameDescription
                hideoutType
                views
                downloads
                comments{
                    _id
                }
                hideoutDateSubmit
                hideoutMasters
                hideoutScreenshot
                votes{
                    score
                }
              }
            }
          }
`;
		try {
			const { data: response } = await context.app.$axios.post(`/api/graphql`, { query });
			return {
				user: response.data.user
			};
		} catch (e) {
			console.log(e);
		}
	},
	components: {
		CardLayout
	},
	methods: {
		getImage (hideout) {
			if (/.jpg|.png|jpeg/gi.test(hideout.hideoutScreenshot)) {
				return hideout.hideoutScreenshot;
			} else {
				return this.$store.getters.getHideout(hideout.hideoutType)['Icon'];
			}
		},
		getHideout (hash) {
			return this.$store.getters.getHideout(hash)['Name'];
		}
	}
};
